import React from 'react';
import { Accordion, Icon } from 'semantic-ui-react'
import { Link } from 'react-router-dom';
import '../App.css'

class Home extends React.Component {
  state = {
    activeIndex: ""
  }

  handleClick = (e, titleProps) => {
    const { index } = titleProps
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  render() {
    const { activeIndex } = this.state
    return(
    <div>
      <h1>Tervetuloa</h1>
      Vaalikausikoneella selvitt mik puolue on nestnyt eduskunnassa lhimmksi omia nkemyksisi.
      <br/>
      <br/>

      Vaalikausikoneen kysymykset on valittu eduskunnan tysistunnon nestyksist. Vastauksesi rinnastuvat puolueiden todelliseen nestyskyttytymiseen. Puolueiden antamat lupaukset eivt vaikuta vaalikoneen tuloksiin.
      <br/>
      <br/>

      Vaalikausikoneessa voit mys verrata puolueiden nestyskyttytymist suhteessa edustajien Ylen vaalikoneessa antamiin vastauksiin. Voit siis arvioida, kuinka hyvin ennen vaaleja annetut lupaukset ovat pitneet.
      <br/>
      <br/>

        <Link
          to="/kone"
        >
          <button className="button">Aloita</button>
        </Link>
        <Accordion style={{paddingTop: "1em", paddingBottom:"1em"}}>
          <Accordion.Title active={activeIndex === 0} index={0} onClick={this.handleClick}>
            <Icon name='dropdown' />
            <b>Miksi puoleen valinta on trkeit?</b>
          </Accordion.Title>
          <Accordion.Content active={activeIndex === 0} style={{marginLeft: "2em"}}>
            <p>
              Ryhmkuri
            </p>
          </Accordion.Content>

          <Accordion.Title active={activeIndex === 1} index={1} onClick={this.handleClick}>
            <Icon name='dropdown' />
            <b>Kuinka poliittinen jrjestelm toimii?</b>
          </Accordion.Title>
          <Accordion.Content active={activeIndex === 1} style={{marginLeft: "2em"}}>
            <p>
              Hallitus/oppositio
            </p>
          </Accordion.Content>
        </Accordion>
    </div>
    )
  }
}

export default Home
